      pointer-events: all;
      position: static;
      display: flex; } }

.nav__link {
  display: block;
  padding: 1rem 0;
  font-size: 2.7rem;
  font-weight: bold;
  background: rgba(0, 0, 0, 0.9);
  color: #ccc; }
  @media (min-width: 50.8125em) {
    .nav__link {
      font-size: 2.1rem;
      background: none;
      color: #222;
      font-weight: 400;
      text-transform: uppercase;
      padding: 1rem 1.4rem; } }
  @media (min-width: 57.5em) {
