.nav__btn.open + .nav__list {
  opacity: 1;
  pointer-events: all; }

.nav__btn {
  width: 6.5rem;
  height: 6rem;
  display: block;
  background: rgba(0, 0, 0, 0.4);
  border: none;
  border-radius: .8rem;
  padding: .3rem .5rem;
  position: fixed;
  top: 2.6rem;
  right: 3.6rem;
  z-index: 100; }
  @media (min-width: 50.8125em) {
