      display: none; } }
  .nav__btn::before {
    content: 'Navigation';
    font-size: 1.3rem;
    color: rgba(110, 110, 110, 0.8);
    position: absolute;
    top: 105%;
    left: 0;
    right: 0; }
  .nav__btn-bars {
    display: block;
    height: .5rem;
    background: rgba(250, 250, 250, 0.7);
    position: relative;
    transition: all .3s ease-in-out; }
    .nav__btn-bars::before, .nav__btn-bars::after {
      content: "";
      display: block;
      height: .5rem;
      background: rgba(250, 250, 250, 0.7);
      position: relative;
      transition: all .3s ease-in-out; }
    .nav__btn-bars::before {
      top: -1.6rem; }
    .nav__btn-bars::after {
      top: 1.1rem; }
  .nav__btn.open {
    background: rgba(244, 244, 244, 0.7); }
  .nav__btn.open .nav__btn-bars {
    background: transparent; }
    .nav__btn.open .nav__btn-bars::before {
      transform: rotate(315deg);
      background: rgba(0, 0, 0, 0.7);
      top: 0; }
    .nav__btn.open .nav__btn-bars::after {
      transform: rotate(-315deg);
      background: rgba(0, 0, 0, 0.7);
      top: -.5rem; }

